<template>
<div>
<v-tabs
      v-model="tab"
      centered
      icons-and-text
    >
      <v-tabs-slider></v-tabs-slider>

      <v-tab href="#tab-1">
        Zbornik
        <!-- <v-icon>mdi-phone</v-icon> -->
      </v-tab>

      <v-tab href="#tab-2" v-if="action == 'edit'">
        Wywz
        <!-- <v-icon>mdi-heart</v-icon> -->
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item
        value="tab-1"
      >
        <v-card flat>
          <v-card-text>
            <validation-observer
    ref="observer"
    v-slot="{ invalid }"
  >
    <form @submit.prevent="addEditTank">
      <validation-provider
        v-slot="{ errors }"
        rules="required"
      >
        <v-text-field
          v-model="tankData.name"
          :error-messages="errors"
          label="Numer zbiornika"
          required
        ></v-text-field>
      </validation-provider>
      <validation-provider
        v-slot="{ errors }"
        rules="required"
      >
        <v-select
          v-model="tankData.manufacturerId"
          :items="tankManufacturerList"
          item-text="name"
          item-value="id"
          :error-messages="errors"
          label="Producent"
          data-vv-name="manufacturer"
          required
        ></v-select>
      </validation-provider>
       <validation-provider
        v-slot="{ errors }"
        rules="required"
      >
        <v-select
          v-model="tankData.capacityId"
          item-text="name"
          item-value="id"
          :items="tankCapacityList"
          :error-messages="errors"
          label="Pojemno"
          data-vv-name="capacity"
          required
        ></v-select>
      </validation-provider>
       <validation-provider
        v-slot="{ errors }"
        rules="required"
      >
        <v-select
          v-model="tankData.ownerId"
          item-text="name"
          item-value="id"
          :items="tankOwnerList"
          :error-messages="errors"
          label="Waciciel"
          data-vv-name="owner"
          required
        ></v-select>
      </validation-provider>
      <validation-provider
        v-slot="{ errors }"
        rules="required"
      >
        <v-select
          v-model="tankData.valveId"
          item-text="name"
          item-value="id"
          :items="tankValveList"
          :error-messages="errors"
          label="Zawr"
          data-vv-name="valve"
          required
        ></v-select>
      </validation-provider>
      <div class="form-row">
        <div class="form-control">
          <label for="productionYear">Rok produkcji
            <datepicker format="yyyy" :minimumView="'year'" :maximumView="'year'" v-model="tankData.productionYear"></datepicker>
          </label>
        </div>
        <div class="form-control">
          <v-radio-group v-model="tankData.pin">
            <v-radio
              label="P"
              value="P"
            >P</v-radio>
            <v-radio
              label="N"
              value="N"
            >N</v-radio>
          </v-radio-group>
        </div>
      </div>
    <v-textarea
          v-model="tankData.comment"
          solo
          label="Uwagi"
        ></v-textarea>
        <v-textarea
          v-model="tankData.workComment"
          solo
          label="Uwagi do wykonanej pracy"
        ></v-textarea>
      <v-btn
        class="mr-4"
        type="submit"
        :disabled="invalid"
      >
        Zapisz
      </v-btn>
    </form>
  </validation-observer>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item
        value="tab-2"
        v-if="action ==='edit'"
      >
        <v-card flat>
          <v-card-text>
            <validation-observer
    ref="observer2"
    v-slot="{ invalid }"
  >
    <form @submit.prevent="tankExport">
      <validation-provider
        v-slot="{ errors }"
        rules="required"
      >
        <v-text-field
          v-model="exportData.city"
          :counter="10"
          :error-messages="errors"
          label="Miasto"
          required
        ></v-text-field>
      </validation-provider>
      <validation-provider
        v-slot="{ errors }"
        rules="required"
      >
        <v-text-field
          v-model="exportData.street"
          :error-messages="errors"
          label="Ulica"
          required
        ></v-text-field>
      </validation-provider>
      <v-btn
        class="mr-4"
        type="submit"
        :disabled="invalid"
      >
        Wywz
      </v-btn>
    </form>
      </validation-observer>

          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
</div>
</template>
<script>
import { mapState } from 'vuex';
import { required, min } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate'
import Datepicker from 'vuejs-datepicker'
// import axios from 'axios';

extend('required', {
    ...required,
    message: 'Pole {_field_} nie moe by puste',
  })

  extend('min', {
    ...min,
    message: 'Minimalna dugo znakw to {length}',
  })
// import History from '../../../../../model/History';
// import Api from '../../../../../model/Api';
// import store from '../../../../../store';

export default {
  name: 'TankForm',
  props: ['formData', 'action', 'parcelId', 'slotId'],
  components: {
    ValidationProvider,
    ValidationObserver,
    Datepicker,
  },
  data() {
    return {
    //   user: this.$session.get('user_info'),
    tab: null,
      tankData: {
        name: '',
        manufacturerId: '',
        capacityId: '',
        ownerId: '',
        valveId: '',
        productionYear:new Date(),
        pin: 'P',
        comment: '',
        workComment: '',
      },
      exportData: {
        city: '',
        street: '',
      },
      // tankManufacturerList:[],
    };
  },

  created() {
    // const self = this;
    this.$store.dispatch('tank/getManufacturerList');
    this.$store.dispatch('tank/getCapacityList');
    this.$store.dispatch('tank/getOwnerList');
    this.$store.dispatch('tank/getValveList');

  },
  mounted() {
    if(this.action === 'edit'){
      this.tankData = this.formData;
      this.tankData.productionYear = new Date(this.tankData.productionYear);
    }
  },
  methods: {
    addEditTank() {
    //   const self = this;
      this.tankData.parcelId = this.parcelId;
      this.tankData.slotId = this.slotId;
      this.tankData.productionYear = this.tankData.productionYear.getFullYear();
      if(this.action === 'edit') {
        this.$store.dispatch('common/editTank', this.tankData);
        this.$emit('addTank');
      }else if(this.action === 'add'){
        this.$store.dispatch('common/addTank', this.tankData);
        this.$emit('addTank');
      }
        // store.dispatch('adminPanel/addUser', data);
    },
    tankExport(){
      this.tankData.parcelId = this.parcelId;
      this.tankData.slotId = this.slotId;
      // this.tankData.productionYear = this.tankData.productionYear.getFullYear();
        this.$store.dispatch('common/exportTank', {
          parcelId: this.parcelId,
          slotId: this.slotId,
          isExported: 1,
          city: this.exportData.city,
          street: this.exportData.street,
          id: this.tankData.id
          });
        this.$emit('addTank');
    }
  },
  computed: {
    ...mapState('tank', [
      'tankManufacturerList',
      'tankCapacityList',
      'tankOwnerList',
      'tankValveList',
    ]),
    ...mapState('authentication', [
      'user',
      'token',
    ]),
    ...mapState([
      'api_url'
    ]),
  },
};
</script>

<style lang="scss">
.v-dialog__container {
    display: unset;
}
  .v-modal {
    padding: 20px;
  }
  .v-toolbar {
    flex: none;
  }
  // .form-row {
  //   display: flex;
  //   justify-content: space-between;
  // }
  // .form-control {
  //   display: flex;
  //   flex-direction: row;
  //   align-self: stretch;
  // }
</style>
