const SET_TOKEN = (state, token) => {
    state.token = token;
  };
const SET_USER = (state, user) => {
    state.user = user;
  };
const SET_SECRET_CONTENT = (state, secretContent) => {
  state.secretContent = secretContent;
};
const RESET = state => {
    state.secretContent = '';
    state.user = {};
    state.token = '';
  }
  export default {
    SET_TOKEN,
    SET_USER,
    SET_SECRET_CONTENT,
    RESET,
  };
