// src/views/SignUp.vue
<template>
  <div class="form-wrapper">
    <validation-observer
      ref="observer"
      v-slot="{ invalid }"
    >
      <form @submit.prevent="signUp">
        <validation-provider
          v-slot="{ errors }"
          name="Login"
          rules="required"
        >
          <v-text-field
            v-model="username"
            :error-messages="errors"
            label="Login"
            required
          ></v-text-field>
        </validation-provider>
        <validation-provider
          v-slot="{ errors }"
          name="password"
          rules="required"
        >
          <v-text-field
            v-model="password"
            ref="password"
            type="password"
            min-length = "6"
            :error-messages="errors"
            label="Haso"
            required
          ></v-text-field>
        </validation-provider>
        <validation-provider
          v-slot="{ errors }"
          name="cofirm"
          rules="required|password:@password"
        >
          <v-text-field
            v-model="password_repeat"
            type="password"
            min-length = "6"
            required
            :error-messages="errors"
            label="Powtrz haso"
          ></v-text-field>
        </validation-provider>
        <!-- <validation-provider
          v-slot="{ errors }"
          name="email"
          rules="required|email"
        >
          <v-text-field
            v-model="email"
            :error-messages="errors"
            label="E-mail"
            required
          ></v-text-field>
        </validation-provider> -->
        <v-btn
          class="mr-4"
          :disabled="invalid"
          type="submit"
        >
          Zarejestruj
        </v-btn>
        <p class="form-question">Masz ju konto ? <a href="#/login">Zaloguj si</a></p>
      </form>
    </validation-observer>
  </div>
</template>
<style lang="scss" scoped>
  .form-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height:70vh;
  span {
    width: 400px;
  }
  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
    padding:20px;
    .v-btn {
      width: 60%;
    }
    .form-question {
      margin-top:30px;
    }
    @media only screen
    and (orientation: portrait) {
      span{
        width: 100%;
      }
      form {
        width: 100%;
      }
    }
  }

}
</style>
<script>
// import AuthService from '@/services/AuthService.js';
import { required, min } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'
 setInteractionMode('eager')
extend('password', {
  params: ['target'],
  validate(value, { target }) {
    return value === target;
  },
  message: 'Hasa musz by takie same.'
});

  extend('required', {
    ...required,
    message: 'Pole {_field_} nie moe by puste',
  })

  extend('min', {
    ...min,
    message: 'Minimalna dugo znakw to {length}',
  })
export default {
  components: {
      ValidationProvider,
      ValidationObserver,
  },
  data() {
    return {
      username: '',
      password: '',
      password_repeat: '',
      msg: ''
    };
  },
  methods: {
    async signUp() {
      try {
        const credentials = {
          username: this.username,
          password: this.password,
          password_repeat: this.password_repeat
        };
        const response = await this.$store.dispatch("authentication/signUp", credentials);
        this.msg = response.msg;
        // this.$router.push('/');
      } catch (error) {
        this.msg = error.response.data.msg;
      }
    }
  }
};
</script>