import axios from 'axios';
// import Vue from "vue";

const loadParcelTanksList = (context, payload) => {
  axios.get(`${context.rootState.api_url}parcel/get-parcel-tanks/${payload}`, {
    headers: {
      Authorization: context.rootState.authentication.token,
    //   apitoken: context.rootState.apitoken,
    //   clientbrowser: Vue.clientbrowser,
    },
  })
    .then((result) => {
      context.commit('SAVE_PARCEL_TANKS_LIST', {data: result.data, id: payload});
    })
    .catch(() => {
      // console.log(error);
    });
};
const loadParcelList = (context) => {
  axios.get(`${context.rootState.api_url}parcel/get-parcel-list`, {
    headers: {
      Authorization: context.rootState.authentication.token,
    //   apitoken: context.rootState.apitoken,
    //   clientbrowser: Vue.clientbrowser,
    },
  })
    .then((result) => {
      context.commit('SAVE_PARCEL_LIST', result.data);
      for(let i=1; i<=result.data.length; i++)
      axios.get(`${context.rootState.api_url}parcel/get-parcel-tanks/${i}`, {
        headers: {
          Authorization: context.rootState.authentication.token,
        //   apitoken: context.rootState.apitoken,
        //   clientbrowser: Vue.clientbrowser,
        },
      })
        .then((result) => {
          context.commit('SAVE_PARCEL_TANKS_LIST', {data: result.data, id: i-1});
        })
        .catch(() => {
          // console.log(error);
        });
    })
    .catch(() => {
      // console.log(error);
    });
};
